<template>
  <div class="information">
    <!-- <h1>information</h1> -->
    <div class="ifo_box">
        <input type="text" name="" id="" placeholder="请输入姓名">
        <input type="text" name="" id="" placeholder="请输入手机号">
        <input type="text" name="" id="" placeholder="房屋面积">
        <input type="text" name="" id="" placeholder="选择城市">
        <p class="botton">立即计算报价</p>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
        fontcolor:{
            color : '$base-color'
        }
    }
  }
}
</script>

<style  lang="scss" scoped>
    @import '../../style/style.scss';
    .information{
        padding: 15px;
        background-color: $base-color;
    }
    .ifo_box{
        width: 100%;
        margin: auto;
        background-color: white;
        border-radius: 10px;
        padding: 20px 15px;
    }
    .ifo_box input{
        width: 100%;
        outline: none;
        border-radius: 4px;
        padding: 5px;
        margin: 8px 0;
        border: 1px solid $base-color;
    }
    .botton{
        text-align: center;
        padding: 10px;
        border-radius: 4px;
        font-weight: 700;
        margin: 8px 0;
        background-color: $base-color;
    }
</style>



